import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Box, CircularProgress, Typography } from "@mui/material";

import { IReduxState } from "../../../store/slices/state.interface";
import { calcUserCollateralDetails } from "../../../store/slices/user-slice";
import { AppDispatch } from "../../../store/store";
import { Trade } from "../../../container/trade";

import { useWeb3Context } from "../../../hooks/web3Context";

function BuyingPower() {
  const dispatch = useDispatch<AppDispatch>();
  const { address, chainID, connected } = useWeb3Context();
  const { perpetualObject } = Trade.useContainer();

  // const buyingPower = useSelector<IReduxState, string>((state) => {
  //   return state.positions.buyingPower;
  // });

  const freeCollateral = useSelector<IReduxState, string>((state) => {
    return state.user.freeCollateral;
  });

  const isUserCollateralCalculated = useSelector<IReduxState, boolean>(
    (state) => {
      return state.user.isUserCollateralCalculated;
    }
  );

  const CalcUserCollateralDetails = function () {
    if (connected === true) {
      dispatch(
        calcUserCollateralDetails({
          address: address,
          networkID: chainID,
          perpetualObject: perpetualObject,
        })
      );
    }
  };

  useEffect(() => {
    CalcUserCollateralDetails();
  }, [connected]);

  return (
    <Box
      display="flex"
      justifyContent="space-between"
      alignItems="center"
      px="16px"
      height="56px"
      border="1px solid rgba(255, 255, 255, 0.12)"
      borderRadius="10px"
    >
      <Typography
        fontSize="13px"
        fontFamily="Montserrat"
        color="rgb(169, 168, 166)"
      >
        Buying Power
      </Typography>
      {!isUserCollateralCalculated && connected && (
        <Box display="flex" alignItems="center" justifyContent="center">
          <CircularProgress size={24} sx={{ color: "#ffffff" }} />
        </Box>
      )}
      {!isUserCollateralCalculated && !connected && (
        <Typography
          fontSize="13px"
          fontFamily="Montserrat"
          color="rgb(236, 232, 227)"
        >
          $
          {isUserCollateralCalculated
            ? (Number(freeCollateral) * 10).toFixed(4)
            : "-"}
        </Typography>
      )}
      {isUserCollateralCalculated && (
        <Typography
          fontSize="13px"
          fontFamily="Montserrat"
          color="rgb(236, 232, 227)"
        >
          ${(Number(freeCollateral) * 10).toFixed(4)}            
        </Typography>
      )}

      {/* <Typography
        fontSize="13px"
        fontFamily="Montserrat"
        color="rgb(236, 232, 227)"
      >
        $
        {isUserCollateralCalculated
          ? (Number(freeCollateral) * 10).toFixed(4)
          : "-"}
      </Typography> */}
    </Box>
  );
}

export default BuyingPower;
